import {NgModule, ModuleWithProviders} from '@angular/core';
import {CommonModule} from '@angular/common';
import {ScrollingModule} from '@angular/cdk/scrolling';

import {AdaptTypeaheadWindowComponent} from './typeahead-window';
import {AdaptTypeaheadDirective} from './typeahead';
import {AdaptTypeaheadModel} from './typeahead.config';
import {AdaptDeviceDetectionService, AdaptHighlightModule, ARIA_LIVE_DELAY, DEFAULT_ARIA_LIVE_DELAY, Live} from '@bmc-ux/adapt-angular';


@NgModule({
  declarations: [
    AdaptTypeaheadDirective,
    AdaptTypeaheadWindowComponent
  ],
  exports: [AdaptTypeaheadDirective, ],
  imports: [CommonModule, ScrollingModule, AdaptHighlightModule],
  entryComponents: [AdaptTypeaheadWindowComponent]
})
export class AdaptTypeaheadSubModule {
  static forRoot(): ModuleWithProviders<AdaptTypeaheadSubModule> {
    return {
      ngModule: AdaptTypeaheadSubModule,
      providers: [
        Live,
        AdaptTypeaheadModel, {
          provide: ARIA_LIVE_DELAY,
          useValue: DEFAULT_ARIA_LIVE_DELAY
        },
        AdaptDeviceDetectionService
      ]
    };
  }
}

export * from './typeahead-window';
export * from './typeahead.config';
export * from './typeahead';
